function modalHide() {
  $('.yi-mask').hide();
  $('.yi-modal').hide();
}

;

function modalShow(idName) {
  $('.yi-mask').show();
  $(idName).show();
}

;
$('.crse-btn .btn-a').not('.disabled').on('click', function () {
  modalShow('#modalExch');
});
$('#modalExch').on('click', '.btnno', function () {
  modalHide();
});